import {iosVhFix} from './utils/ios-vh-fix';
import {initModals} from './modules/modals/init-modals';
import {Form} from './modules/form-validate/form';
import {initMobileMenu} from './modules/init-mobile-menu';
import {initSliderNews} from './modules/init-slider-news';
import {initSliderReviews} from './modules/init-slider-reviews';
import {initToggleClassCard} from './modules/init-toggle-class-card';
import {initTabs} from './modules/init-tabs';
import {initNavModules} from './modules/init-resize-nav';
import {initMoreNav} from './modules/init-more-nav';
import {initVideoPlay} from './modules/init-video-play';

// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  // ---------------------------------

  iosVhFix();

  // Modules
  // ---------------------------------

  //       'DOMContentLoaded',     'load'
  //  load   ,      
  window.addEventListener('load', () => {
    initModals();
    const form = new Form();
    window.form = form;
    form.init();
    initMobileMenu();
    initSliderNews();
    initSliderReviews();
    initToggleClassCard();
    initTabs();
    initNavModules();
    initMoreNav();
    initVideoPlay();
  });
});

